
html,
body {
  width: 100%;
  height: 100%;
  background-image: url("https://images.unsplash.com/photo-1516331138075-f3adc1e149cd?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=3a1bca94717c3a2c1c721a72da13d63f&auto=format&fit=crop&w=500&q=60");
  background-size: initial;
  line-height: inherit;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.grid-container {
  display: grid;
  grid-template-columns: repeat(20, 55px);
  -ms-flex-pack: center;
      justify-content: center;
  height: auto;
  width: inherit;
  border: 1px dotted black;
  -ms-grid-row-align: stretch;
}

.cell {
  border: 1px solid rgba(31, 27, 85, 0.664);
  border-bottom-style: inherit;
  border-bottom-style: outset;
  -webkit-border-image: 0vw;
       -o-border-image: 0vw;
          border-image: 0vw;
  border-radius: 1px;
  width: auto;
  background-color: whitesmoke;
  padding-bottom: 30px;
}

.cell.alive {
  background-color: rgba(87, 9, 9, 0.356);
  width: 54px;
  height:  4px;
}

.cell.dead {
  background-color: rgba(255, 255, 255, 0.548);
  border-radius: 0.4rem;
  width: 54px;
  height:  4px;
  padding-top: 5px;
}

.alive {
  background-color: rgb(98, 90, 90);
}
.element {
  height: 100%;
  width: 100%;
  -webkit-animation-name: stretch;
          animation-name: stretch;
  -webkit-animation-duration: 5s;
          animation-duration: 5s; 
  -webkit-animation-timing-function: ease-in-out; 
          animation-timing-function: ease-in-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: inherit;
          animation-direction: inherit;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-fill-mode: none;
          animation-fill-mode: none;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

@-webkit-keyframes stretch {
  50% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }

  
}

@keyframes stretch {
  50% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }

  
}

.buttons{
  padding-bottom: 4%;
}
h1{
  color: white;
  font-weight: bolder;
  font-family: cursive;
}


